export default {
    name: 'MusicPlayer',
    data() {
        return {
            poster: 'https://zb.luckyme.com.cn/static/mp3/wulinwaizhuan/logo.jpg',
            name: '武林外传',
            author: '第一回',
            src: '',
            nowPlay: 1,
            progress: 0,
            playStatus:0,
            loop:0,
            animationData:{},//旋转动画
            currentTime:0,
            duration:0,
        }
    },
    filters:{
        timeFormat(time){
            let minutes=parseInt(time/60);
            let seconds=parseInt(time%60);
            return minutes+':'+seconds
        }
    },
    methods: {
        /*旋转*/
        animationPlay(){
            let animation = uni.createAnimation({
                duration: 10000,
                timingFunction: 'linear',
            })
            this.animation = animation
            animation.rotate(360).step()
            this.animationData = animation.export()
        },
        togglePlay(){
            if(!this.playStatus){
                this.audioCtx.play()
                console.log('播放')
                this.animationPlay()
            }else {
                this.audioCtx.pause()
                console.log('暂停')
            }
            this.playStatus=!this.playStatus
        },
        /*播放*/
        audioPlay() {
            this.audioCtx.play()
        },
        /*上一曲*/
        audioLast() {
            if(this.nowPlay>1){
                this.audioSelect(this.nowPlay - 1)
                console.log(this.nowPlay)
            }
        },
        /*下一曲*/
        audioNext() {
            this.audioSelect(this.nowPlay + 1)
            console.log(this.nowPlay)

        },
        /*选曲*/
        audioSelect(num) {
            this.nowPlay=num
            this.audioCtx.src='https://zb.luckyme.com.cn/static/mp3/wulinwaizhuan/' + num + '.m4a';
            this.audioCtx.onCanplay(() => {
                console.log('准备就绪')
                this.audioPlay()
                this.playStatus=1
                this.currentTime=this.audioCtx.currentTime
                this.duration=this.audioCtx.duration
            })
        },
        /*暂停*/
        audioPause() {
            this.audioCtx.pause()
        },
        /*跳过开头*/
        audioJump() {
            this.audioCtx.seek(105)
            this.audioPlay()
        },
        /*重播*/
        audioStart() {
            this.audioCtx.seek(0)
        },
        /*跳到结尾*/
        audioEnd() {
            this.audioCtx.seek(2855)
        },
        /*time更新触发*/
        audioTimeupdate() {
            this.progress=(this.audioCtx.currentTime / this.audioCtx.duration) * 100
            this.currentTime=this.audioCtx.currentTime
        },
        /*结束*/
        audioEnded() {
            console.log('结束~')
        },
        /*出错*/
        audioErr(err) {
            console.log(err)
        },

        /*循环*/
        audioLoop(){
            this.loop=!this.loop
            let tip='循环播放';
            if(!this.loop){
                tip='顺序播放'
            }
            uni.showToast({
                title: tip,
                duration: 2000,
                icon:'none',
                position:'bottom'
            });
        }
    },
    mounted() {
        this.audioCtx = uni.createInnerAudioContext('myAudio');
        this.audioCtx.autoplay = false;

        this.audioCtx.src = 'https://zb.luckyme.com.cn/static/mp3/wulinwaizhuan/' + this.nowPlay + '.m4a';
        this.audioCtx.onTimeUpdate((e)=>{
            this.audioTimeupdate()
        })

        this.audioCtx.onPlay(() => {
            this.duration=this.audioCtx.duration
            console.log('开始播放');
        });
        this.audioCtx.onError((res) => {
            console.log(res.errMsg);
            console.log(res.errCode);
        });
        // this.audioCtx.play();


        console.log(this.$store.state.MusicPlayStatus)
    }
}
